
.hidden{
  display: none !important;
  visibility: hidden;
}

#sideNav{
  position: absolute;
  height: 100%;
  background-color: $menu-color;
  z-index: 10;
  float:left;

}
.navBurger{
  width:100%;
  display: flex;
  align-items: center;
  div{
    display: inline-block;
  }
  .icon{
    width: $nav-width;
  }
  i{
    margin: 0 auto;
  }
}
.help{
  .help-section{
    width:100%;
    display: flex;
    align-items: center;
    div{
      display: inline-block;
    }
    .icon{
      width: $nav-width;
    }
    i{
      margin: 0 auto;
    }
    a{
      display: flex;
      align-items: center;
      text-decoration: none;
    }
    .icon-text-help{
      color: $nav-help-font-color;
      font-family: $nav-font;
    }
  }
}
